
.todoListMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}
.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #0066FF;
  color: #FFF;
  border: 2px solid #0066FF;
}
.todoListMain .header button:hover {
  background-color: #003399;
  border: 2px solid #003399;
  cursor: pointer;
}
.todoListMain .theList {
  list-style: none;
  padding-left: 0;
  width: 250px;
} 
.todoListMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 30px;
  border-radius: 5px;
  list-style: none;

}
ul.theList {
  padding: 0;
}

.todoListMain .theList li i{
  color: transparent;
  display: none;
  float: right;
  
}
 
.todoListMain .theList li:hover i {
  display: block;
  color: red; 
  cursor: pointer;
  transition: all .2s ease-out;
}

.todoListMain .theList li .date{
  color: rgb(46, 46, 46); 
  position: absolute;
  margin-top: 20px;
  font-size: 12px;
  
}